.table-desc {
  font-size: 15px;
  line-height: 20px;
  font-weight: 400;
  color: $color-secondary--light;
  border-collapse: collapse;
}

.table-desc__col {
  border-bottom: 1px $color-service solid;
  padding: 10px;
  padding-right: 28px;
  padding-left: 0;

  @media (min-width: $tablet-width) {
    padding-right: 0;
  }
}
